
.wp-duration{display:-webkit-box;display:-ms-flexbox;display:flex;border-bottom:thin solid white;-webkit-box-align:center;-ms-flex-align:center;align-items:center
}
.wp-duration strong small{font-weight:100
}
.wp-duration[class*='status-']{border-left:solid 4px #ddd
}
.wp-duration.status-5{border-left-color:#0b58a2
}
.wp-duration .icon-comment{color:#CCC
}
.wp-duration .icon-comment.with-comment{color:#000
}
.left{-webkit-box-flex:0;-ms-flex:0 0 35px;flex:0 0 35px;overflow:hidden;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;padding-left:8px;border-left:solid #fff thin
}
.total{margin-left:auto;font-size:1.4em;font-weight:700;padding-right:.5em
}
.total em{font-size:.5em;font-weight:100;line-height:2em
}
[class*="icon-"]{text-align:center
}
.icon-trash,.icon-edit{cursor:pointer;text-align:center
}
.icon-trash:hover,.icon-edit:hover{background:#0b58a2;color:white
}



